import back from '../../components/back.vue';
export default {
	data() {
		return {
			pageNameFlag:1,
			showFlag:0,
			pageName:'终端状态消息详情',
			radioFlag:0,
			devIndex:'',
			timeID:'',
			showTime:'',
			errorFlag:0,
			devMsg:{}
			
		}
	},
	methods: {
		devMsgInfo(){
			var that = this;
			uni.request({
				url: that.serverUrl+'/deviceMsg/devMsgInfo',
				method: 'POST',
				data: {
					devIndex: that.devIndex,
					timeID:that.timeID
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				sslVerify: false,
				success: (res) => {
					uni.hideLoading();
					if (res.data.resultCode == '10000') {
							that.devMsg = eval(res.data.data);
					} else {
						that.errorFlag = 1
					}
				},
				fail: (re) => {
					that.errorFlag = 1
				}
			});
		},
		retry(){
			this.errorFlag=0;
			this.devMsgInfo();
		}
	},
	onLoad(e) {
		uni.showLoading({
			mask:true,
			});
		this.devIndex=e.devIndex;
		this.timeID=e.timeID;
		this.showTime=e.showTime;
		this.devMsgInfo();
	},
	components:{
		back
	}
}
